import 'bootstrap';
import Semantic from 'vendor/semantic/semantic.min';
import Swiper from 'swiper';
import semantic from 'semantic-ui';
import noUiSlider from 'nouislider';
import $ from 'jquery';
import 'owl.carousel2';



$(document).ready(
    function () {
        console.log('hello world');
        $('.ui.sticky').sticky();
        // Barba.Pjax.start()
        $(window).scroll(function () {
            if ($(this).scrollTop() > 40) {
                if (!$('.navbar').hasClass('is-fixed')) {
                    $('.navbar').stop().addClass('is-fixed');

                }
            } else {
                $('.navbar').removeClass('is-fixed').css('top', '0');
            }
        });

        let searchbar = $('.inp input');
        $('.search-btn').click(function () {
            if (!searchbar.hasClass('activate')) {
                searchbar.addClass('activate');
                searchbar.removeClass('close');
            } else {
                searchbar.removeClass('activate');
                searchbar.addClass('close');
            }

        });


        // video-background
        // ----------------------------
        var min_w = 300;
        var vid_w_orig;
        var vid_h_orig;

        $(function () {

            vid_w_orig = parseInt($('video').attr('width'));
            vid_h_orig = parseInt($('video').attr('height'));

            $(window).resize(function () {
                fitVideo();
            });
            $(window).trigger('resize');

        });

        function fitVideo() {

            $('#video-viewport').width($('.main').width());
            $('#video-viewport').height($('.main').height());

            var scale_h = $('.main').width() / vid_w_orig;
            var scale_v = $('.main').height() / vid_h_orig;
            var scale = scale_h > scale_v ? scale_h : scale_v;

            if (scale * vid_w_orig < min_w) {
                scale = min_w / vid_w_orig;
            }
            ;

            $('video').width(scale * vid_w_orig);
            $('video').height(scale * vid_h_orig);

            $('#video-viewport').scrollLeft(($('video').width() - $('.main').width()) / 2);
            $('#video-viewport').scrollTop(($('video').height() - $('.main').height()) / 2);

        }

        // -------------------------

        var galleryThumbs = new Swiper('.gallery-thumbs', {
            direction: 'vertical',
            spaceBetween: 15,
            slidesPerView: 4,
            setWrapperSize: true,
            freeMode: false,
            watchSlidesVisibility: false,
            watchSlidesProgress: false,
        });
        var galleryTop = new Swiper('.gallery-top', {
            speed: 600,
            spaceBetween: 15,
            // effect: 'fade',
            parallax: true,
            loop: false,
            thumbs: {
                swiper: galleryThumbs
            }
        });

        $('.ui.rating')
            .rating();


        $('a[href*=\\#]').on('click', function (e) {
            e.preventDefault();
            console.log($('a[href*=\\#]').attr('id'), this);
            $('html, body').animate({scrollTop: $($(this).attr('href')).offset().top}, 500, 'linear');
        });

        $('#user').click(function () {
            $('.login-modal').show();
        });


        $('input').focus(function () {
            $(this).parent().addClass('focus')
        }).blur(function () {
            if ($(this).val() === '') {
                $(this).parent().removeClass('focus')
            }
        });


        function hide_pass(btn_name, pass_name) {
            $(btn_name).click(function () {
                $(pass_name).attr('type', $(this).is(':checked') ? 'text' : 'password')
            });
        }

        hide_pass('#method', '#pass_signIn');
        hide_pass('#method_signUp', '#pass_signUp');
        hide_pass('#method_confirm', '#pass_confirm');

        $('.close-container').click(() => {
            $('.login-modal').fadeOut(700);
        });

        let count = 0;

        $('#basket').click(function () {

            $('.coupled.modal')
                .modal({
                    allowMultiple: false
                });
            // open second modal on first modal buttons
            $('.second.modal')
                .modal('setting', 'transition', 'horizontal flip')
                .modal({
                    closable: false,
                    onApprove: function () {
                        if (count >= 1) {
                            console.log('hide', count, '#section' + (count + 1));
                            $('#section' + (count + 1)).animate({left: '110%'}, {
                                duration: 500,
                                complete: () => {
                                    var currentStep = '.step:nth-child(' + count + ')';
                                    $('#section' + count + 1).hide();
                                    $('#section' + count).show().animate(
                                        {
                                            left: 0
                                        }, 500);
                                    console.log($(currentStep).offset().left);
                                    $('.steps').animate({scrollLeft: -($(currentStep).offset().left)}, 800);
                                    $(currentStep).removeClass('completed').addClass('active');
                                    $('.step:nth-child(' + (count + 1) + ')').removeClass('active');
                                    count--;
                                }
                            });
                        } else {
                            $('.ui.longer.first.modal')
                                .modal('setting', 'transition', 'horizontal flip')
                                .modal('show');
                        }
                        return false;
                    }
                })
                .modal('attach events', '.first.modal .action .approve.button');

            $('.ui.longer.first.modal')
                .modal('setting', 'transition', 'horizontal flip')
                .modal('show');
        });

        $('.back').click(() => {
            $('.ui.longer.modal')
                .modal('hide');
        });

        $('#section2 .ui.dropdown')
            .dropdown({
                allowAdditions: true,
                onChange: function () {
                    $('.shipping-cost').fadeIn(1000);
                }
            });

        $('#section3 .ui.dropdown')
            .dropdown({
                allowAdditions: true,
                onChange: function () {

                }
            });


        $('.next-step').click(function () {
            count++;
            var currentStep = '.step:nth-child(' + count + ')';
            if (count <= 3) {
                $('#section' + count).animate({left: '-110%'}, {
                    duration: 500,
                    complete: () => {
                        $('#section' + count).hide();
                        $('#section' + (count + 1)).show().animate(
                            {
                                left: 0
                            }, 500);
                        console.log($(currentStep).offset().left);
                        $('.steps').animate({scrollLeft: $(currentStep).offset().left + 100}, 800);
                        $(currentStep).removeClass('active').addClass('completed');
                        if (count === 3) {
                            $('.step:nth-child(' + (count + 1) + ')').removeClass('disabled').addClass('active');
                            $('.shape.confirm').shape('flip left');
                        } else {
                            $('.step:nth-child(' + (count + 1) + ')').addClass('active');
                        }
                    }
                });
            } else {
                count = 3;
            }
        });

        $('.ui.dropdown.category')
            .dropdown({
                allowAdditions: true
            });

        var slider = document.getElementById('slider');

        // noUiSlider.create(slider, {
        //     start: [20, 80],
        //     connect: true,
        //     range: {
        //         'min': 0,
        //         'max': 100
        //     }
        // });

        $('.ui.accordion')
            .accordion({
                selector: {
                    trigger: '.title'
                }
            });

        $('.ui.menu')
            .accordion({
                selector: {
                    trigger: '.title-two .icon'
                }
            });

        $('.toggle-view-big').click(() => {
            if (!$('.toggle-view-big').hasClass('isActive')) {
                $('.toggle-view-small').removeClass('isActive');
                $('.toggle-view-big').addClass('isActive');
                $('.ui.grid.catalog').removeClass('four column').addClass('two column');
                $('.product.column').css('height', 780);
            }
        });

        $('.toggle-view-small').click(() => {
            if (!$('.toggle-view-small').hasClass('isActive')) {
                $('.toggle-view-big').removeClass('isActive');
                $('.toggle-view-small').addClass('isActive');
                $('.ui.grid.catalog').removeClass('two column').addClass('four column');
                $('.product.column').css('height', 270);
            }
        });


        // owl-carousel


        $(document).ready(function() {

            var sync1 = $("#sync1");
            var sync2 = $("#sync2");
            var slidesPerPage = 4; //globaly define number of elements per page
            var syncedSecondary = true;

            sync1.owlCarousel({
                items: 1,
                slideSpeed: 2000,
                nav: true,
                autoplay: false,
                loop: true,
                responsiveRefreshRate: 200,
                navText: ['<svg width="100%" height="100%" viewBox="0 0 11 20"><path style="fill:none;stroke-width: 1px;stroke: #000;" d="M9.554,1.001l-8.607,8.607l8.607,8.606"/></svg>', '<svg width="100%" height="100%" viewBox="0 0 11 20" version="1.1"><path style="fill:none;stroke-width: 1px;stroke: #000;" d="M1.054,18.214l8.606,-8.606l-8.606,-8.607"/></svg>'],
            }).on('changed.owl.carousel', syncPosition);

            sync2
                .on('initialized.owl.carousel', function() {
                    sync2.find(".owl-item").eq(0).addClass("current");
                })
                .owlCarousel({
                    items: slidesPerPage,
                    smartSpeed: 200,
                    slideSpeed: 500,
                    slideBy: slidesPerPage, //alternatively you can slide by 1, this way the active slide will stick to the first item in the second carousel
                    responsiveRefreshRate: 100
                }).on('changed.owl.carousel', syncPosition2);

            function syncPosition(el) {
                //if you set loop to false, you have to restore this next line
                //var current = el.item.index;

                //if you disable loop you have to comment this block
                var count = el.item.count - 1;
                var current = Math.round(el.item.index - (el.item.count / 2) - .5);

                if (current < 0) {
                    current = count;
                }
                if (current > count) {
                    current = 0;
                }

                //end block

                sync2
                    .find(".owl-item")
                    .removeClass("current")
                    .eq(current)
                    .addClass("current");
                var onscreen = sync2.find('.owl-item.active').length - 1;
                var start = sync2.find('.owl-item.active').first().index();
                var end = sync2.find('.owl-item.active').last().index();

                if (current > end) {
                    sync2.data('owl.carousel').to(current, 100, true);
                }
                if (current < start) {
                    sync2.data('owl.carousel').to(current - onscreen, 100, true);
                }
            }

            function syncPosition2(el) {
                if (syncedSecondary) {
                    var number = el.item.index;
                    sync1.data('owl.carousel').to(number, 100, true);
                }
            }

            sync2.on("click", ".owl-item", function(e) {
                e.preventDefault();
                var number = $(this).index();
                sync1.data('owl.carousel').to(number, 300, true);
            });
        });

    }
);



